
import { defineComponent } from "vue";
import ErrorMessage from "@/components/ErrorMessage.vue";
import ValidateError from "@/components/ValidateError.vue";
import {
  showCouponApi,
  updateCouponApi,
  destroyCouponApi,
} from "@/api/coupons";

export default defineComponent({
  name: "CouponUpdate",
  components: {
    ErrorMessage,
    ValidateError,
  },
  data: () => ({
    isShow: false,
    message: "",
    submitDisable: false,
    validateErrors: {} as any,
    items: {} as any,
    coupon: {} as any,
    categories: [] as any[],
  }),
  mounted: async function () {
    // coupon
    const result = await showCouponApi(this.$route.params.id);
    if (!result.success) {
      this.commonError(result);
      return;
    }
    this.coupon = result.data;
    this.setInitData();
    this.isShow = true;
  },
  methods: {
    commonError: function (result: any = null): void {
      if (result.status === 422) {
        this.validateErrors = result.data;
      }
      if (result.message !== "") {
        this.message = result.message;
      }
    },
    setInitData: function () {
      this.items.name = this.coupon.name;
      this.items.code = this.coupon.code;
      this.items.discount_price = this.coupon.discount_price;
      this.items.expired_at = this.coupon.expired_at;
    },
    update: async function () {
      this.submitDisable = true;
      const result = await updateCouponApi(this.$route.params.id, this.items);
      this.submitDisable = false;
      if (!result.success) {
        this.commonError(result);
        return;
      }
      alert("更新しました");
    },
    destroy: async function () {
      this.submitDisable = true;
      const result = await destroyCouponApi(this.$route.params.id);
      this.submitDisable = false;
      if (!result.success) {
        this.commonError(result);
        return;
      }
      alert("削除しました");
      this.$router.push({
        name: "CouponIndex",
      });
    },
  },
});
